//这边基本上引入需要使用的资源less，api，需要使用的模块等等。

class markdataexport extends Interstellar.modelBase {
    constructor(app) {
        super(app)
        this.apiData={page:1,pageSize:10,type:window.location.hash.indexOf('markdataexport')!==-1?1:3}
        this.condition =[
            [{ "type": "dobuledropdown", "name": "projectIdList", "showname": "项目名称", "datatype": "obj", "data": [], "key": "projectIdList", "out": true ,"input":true}],
            [{ "type": "dobuledropdown", "name": "taskIdList", "showname": "任务名称", "datatype": "obj", "data": [], "key": "taskIdList", "out": true,"input":true }],
            [{ "type": "dropdown", "name": "algPreAnnotation", "showname": "任务类型", "datatype": "obj", "data": [{val: '人工标注', idx: 0}, {val: '算法标注',idx: 1}], "key": "algPreAnnotation", "out": true }],
            [{ "type": "time", "name": "inspectTime", "showname": "", "datatype": "obj", "data": null, "key": "inspectTime", "out": true }],
        ]
        this.tableconfig={
            icon:{
                "projectName": { name: '<span data-i18n="age" data-name="年龄">项目名称</span>', type: 'link',code:'checkid', w: '13%', ww: '13%',n:"40" },
                "name": { name: '<span data-i18n="age" data-name="年龄">任务名称</span>', type: 'text',code:'pid', w: '12%', ww: '12%', },
                "algPreAnnotation": { name: '<span data-i18n="age" data-name="年龄">任务类型</span>', type: 'text',code:'pname', w: '8%', ww: '8%' },
                "remark": { name: '<span data-i18n="age" data-name="年龄">任务备注</span>', type: 'text',code:'psex', w: '12%', ww: '12%' },
                "startTime": { name: '<span data-i18n="age" data-name="年龄">任务发布时间</span>', type: 'text',code:'age', w: '12%', ww: '12%' },
                "endTime": { name: '<span data-i18n="age" data-name="年龄">任务到期时间</span>', type: 'text',code:'age', w: '12%', ww: '12%' },
                "projectModality": { name: '<span data-i18n="aidiag" data-name="智能诊断">样本类型</span>', type: 'text',code:'positive', w: '13%', ww: '13%' },
                "seriesTotalNum": { name: '<span data-i18n="aidiag" data-name="智能诊断">任务样本数量</span>', type: 'text',code:'positive', w: '9%', ww: '9%' },
                "seriesSubmittedNum": { name: '<span data-i18n="aidiag" data-name="智能诊断">已提交序列数</span>', type: 'text',code:'positive', w: '9%', ww: '9%' },
            },
            type:'center',
            actionicon:{
                "operation": { name: '<span data-i18n="action" data-name="操作">操作</span>', type: 'action',code:'action', w: '100%', ww: '100%' }
            }
        }
        this.listicon={
            action:{
                danchuchuangkou: { dis: 'inline', link: 'noLink',titleText:'导出全部标注提交结果'}
            }
        }
    }
}
module.exports = markdataexport;



